"use client"

import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Table } from "@/components/ui/table"
import { useState } from "react"
import { useBooks } from "../hooks/useBooks"

interface Book {
  id: number;
  title: string;
  author: string;
  isbn: string;
  genre: string;
  status: string;
}

export default function Books() {
  const { books, loading, error, addBook, updateBook, deleteBook } = useBooks()
  const [showNewBookForm, setShowNewBookForm] = useState(false)
  const [showEditForm, setShowEditForm] = useState(false)
  const [selectedBook, setSelectedBook] = useState<Book | null>(null)
  
  // Add state for form inputs
  const [newBook, setNewBook] = useState({
    title: "",
    author: "",
    isbn: "",
    genre: ""
  })

  const handleNewBook = () => {
    setShowNewBookForm(true)
  }

  const handleEdit = (book: Book) => {
    setSelectedBook(book)
    setShowEditForm(true)
  }

  const handleDelete = async (bookId: number) => {
    if (confirm("Are you sure you want to delete this book?")) {
      try {
        await deleteBook(bookId)
      } catch (error) {
        alert("Failed to delete book")
      }
    }
  }

  const handleAddBook = async () => {
    try {
      await addBook({ ...newBook, status: "Available" })
      setNewBook({ title: "", author: "", isbn: "", genre: "" })
      setShowNewBookForm(false)
    } catch (error) {
      alert("Failed to add book")
    }
  }

  const handleUpdateBook = async () => {
    if (!selectedBook) return

    try {
      await updateBook(selectedBook.id, selectedBook)
      setShowEditForm(false)
      setSelectedBook(null)
    } catch (error) {
      alert("Failed to update book")
    }
  }

  if (loading) {
    return (
      <div className="container mx-auto p-4">
        <div className="text-center">Loading...</div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="container mx-auto p-4">
        <div className="text-center text-red-600">Error: {error}</div>
      </div>
    )
  }

  return (
    <div className="container mx-auto">
      <div className="flex justify-between items-center mb-8">
        <h1 className="text-4xl font-bold text-red-950">Books Management</h1>
        <Button 
          className="bg-red-950 hover:bg-red-900 text-white"
          onClick={handleNewBook}
        >
          Add New Book
        </Button>
      </div>

      {/* Quick Stats */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
        <Card className="p-6 bg-red-50 border-red-100">
          <h3 className="text-lg font-semibold text-red-900">Total Books</h3>
          <p className="text-3xl font-bold text-red-950">{books.length}</p>
          <p className="text-sm text-red-700 mt-2">In collection</p>
        </Card>
        
        <Card className="p-6 bg-red-50 border-red-100">
          <h3 className="text-lg font-semibold text-red-900">Available</h3>
          <p className="text-3xl font-bold text-red-950">
            {books.filter(book => book.status === "Available").length}
          </p>
          <p className="text-sm text-red-700 mt-2">Ready to borrow</p>
        </Card>
        
        <Card className="p-6 bg-red-50 border-red-100">
          <h3 className="text-lg font-semibold text-red-900">Borrowed</h3>
          <p className="text-3xl font-bold text-red-950">
            {books.filter(book => book.status === "Borrowed").length}
          </p>
          <p className="text-sm text-red-700 mt-2">Currently on loan</p>
        </Card>
      </div>

      {/* Search and Filter */}
      <Card className="p-6 mb-8 border-red-100">
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <div>
            <label className="block text-sm font-medium text-red-900 mb-2">
              Search Books
            </label>
            <Input 
              type="text" 
              placeholder="Search by title, author, or ISBN"
              className="border-red-200 focus:border-red-500 focus:ring-red-500"
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-red-900 mb-2">
              Genre
            </label>
            <select className="w-full border-red-200 rounded-md focus:border-red-500 focus:ring-red-500">
              <option value="">All Genres</option>
              <option value="fiction">Fiction</option>
              <option value="non-fiction">Non-Fiction</option>
              <option value="science">Science</option>
              <option value="history">History</option>
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium text-red-900 mb-2">
              Status
            </label>
            <select className="w-full border-red-200 rounded-md focus:border-red-500 focus:ring-red-500">
              <option value="">All Status</option>
              <option value="available">Available</option>
              <option value="borrowed">Borrowed</option>
            </select>
          </div>
        </div>
      </Card>

      {/* Books Table */}
      <Card className="border-red-100">
        <div className="overflow-x-auto">
          <Table>
            <thead>
              <tr className="bg-red-50">
                <th className="text-left p-4 text-red-900">Title</th>
                <th className="text-left p-4 text-red-900">Author</th>
                <th className="text-left p-4 text-red-900">ISBN</th>
                <th className="text-left p-4 text-red-900">Genre</th>
                <th className="text-left p-4 text-red-900">Status</th>
                <th className="text-left p-4 text-red-900">Actions</th>
              </tr>
            </thead>
            <tbody>
              {books.map((book) => (
                <tr key={book.id} className="border-b border-red-100">
                  <td className="p-4">{book.title}</td>
                  <td className="p-4">{book.author}</td>
                  <td className="p-4">{book.isbn}</td>
                  <td className="p-4">{book.genre}</td>
                  <td className="p-4">
                    <span className={`px-2 py-1 rounded-full text-sm ${
                      book.status === "Available" ? "bg-green-100 text-green-800" : "bg-yellow-100 text-yellow-800"
                    }`}>
                      {book.status}
                    </span>
                  </td>
                  <td className="p-4">
                    <div className="flex gap-2">
                      <Button 
                        variant="outline" 
                        className="text-red-950 border-red-200 hover:bg-red-50"
                        onClick={() => handleEdit(book)}
                      >
                        Edit
                      </Button>
                      <Button 
                        variant="outline" 
                        className="text-red-950 border-red-200 hover:bg-red-50"
                        onClick={() => handleDelete(book.id)}
                      >
                        Delete
                      </Button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </Table>
        </div>
      </Card>

      {/* New Book Form Modal */}
      {showNewBookForm && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4">
          <Card className="w-full max-w-md p-6 bg-white">
            <h2 className="text-2xl font-bold text-red-950 mb-4">Add New Book</h2>
            <div className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-red-900 mb-2">
                  Title
                </label>
                <Input 
                  type="text" 
                  value={newBook.title}
                  onChange={(e) => setNewBook({...newBook, title: e.target.value})}
                  className="border-red-200 focus:border-red-500 focus:ring-red-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-red-900 mb-2">
                  Author
                </label>
                <Input 
                  type="text" 
                  value={newBook.author}
                  onChange={(e) => setNewBook({...newBook, author: e.target.value})}
                  className="border-red-200 focus:border-red-500 focus:ring-red-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-red-900 mb-2">
                  ISBN
                </label>
                <Input 
                  type="text" 
                  value={newBook.isbn}
                  onChange={(e) => setNewBook({...newBook, isbn: e.target.value})}
                  className="border-red-200 focus:border-red-500 focus:ring-red-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-red-900 mb-2">
                  Genre
                </label>
                <select 
                  value={newBook.genre}
                  onChange={(e) => setNewBook({...newBook, genre: e.target.value})}
                  className="w-full border-red-200 rounded-md focus:border-red-500 focus:ring-red-500"
                >
                  <option value="">Select Genre</option>
                  <option value="Fiction">Fiction</option>
                  <option value="Non-Fiction">Non-Fiction</option>
                  <option value="Science">Science</option>
                  <option value="History">History</option>
                </select>
              </div>
              <div className="flex gap-2 justify-end mt-6">
                <Button 
                  variant="outline" 
                  className="text-red-950 border-red-200 hover:bg-red-50"
                  onClick={() => setShowNewBookForm(false)}
                >
                  Cancel
                </Button>
                <Button 
                  className="bg-red-950 hover:bg-red-900 text-white"
                  onClick={handleAddBook}
                >
                  Add Book
                </Button>
              </div>
            </div>
          </Card>
        </div>
      )}

      {/* Edit Book Form Modal */}
      {showEditForm && selectedBook && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4">
          <Card className="w-full max-w-md p-6 bg-white">
            <h2 className="text-2xl font-bold text-red-950 mb-4">Edit Book</h2>
            <div className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-red-900 mb-2">
                  Title
                </label>
                <Input 
                  type="text" 
                  value={selectedBook.title}
                  onChange={(e) => setSelectedBook({...selectedBook, title: e.target.value})}
                  className="border-red-200 focus:border-red-500 focus:ring-red-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-red-900 mb-2">
                  Author
                </label>
                <Input 
                  type="text" 
                  value={selectedBook.author}
                  onChange={(e) => setSelectedBook({...selectedBook, author: e.target.value})}
                  className="border-red-200 focus:border-red-500 focus:ring-red-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-red-900 mb-2">
                  ISBN
                </label>
                <Input 
                  type="text" 
                  value={selectedBook.isbn}
                  onChange={(e) => setSelectedBook({...selectedBook, isbn: e.target.value})}
                  className="border-red-200 focus:border-red-500 focus:ring-red-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-red-900 mb-2">
                  Genre
                </label>
                <select 
                  value={selectedBook.genre}
                  onChange={(e) => setSelectedBook({...selectedBook, genre: e.target.value})}
                  className="w-full border-red-200 rounded-md focus:border-red-500 focus:ring-red-500"
                >
                  <option value="">Select Genre</option>
                  <option value="Fiction">Fiction</option>
                  <option value="Non-Fiction">Non-Fiction</option>
                  <option value="Science">Science</option>
                  <option value="History">History</option>
                </select>
              </div>
              <div>
                <label className="block text-sm font-medium text-red-900 mb-2">
                  Status
                </label>
                <select 
                  value={selectedBook.status}
                  onChange={(e) => setSelectedBook({...selectedBook, status: e.target.value})}
                  className="w-full border-red-200 rounded-md focus:border-red-500 focus:ring-red-500"
                >
                  <option value="Available">Available</option>
                  <option value="Borrowed">Borrowed</option>
                </select>
              </div>
              <div className="flex gap-2 justify-end mt-6">
                <Button 
                  variant="outline" 
                  className="text-red-950 border-red-200 hover:bg-red-50"
                  onClick={() => {
                    setShowEditForm(false)
                    setSelectedBook(null)
                  }}
                >
                  Cancel
                </Button>
                <Button 
                  className="bg-red-950 hover:bg-red-900 text-white"
                  onClick={handleUpdateBook}
                >
                  Save Changes
                </Button>
              </div>
            </div>
          </Card>
        </div>
      )}
    </div>
  )
}
